.nav-styled {
    padding: 2rem 1.5rem;
    width: 374px;
    height: 100%;
    background: rgba(252, 246, 249, 0.78);
    border: 3px solid #FFFFFF;
    backdrop-filter: blur(4.5px);
    border-radius: 32px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 2rem;
}

/* User container */
.nav-styled .user-con {
    height: 100px;
    display: flex;
    align-items: center;
    gap: 1rem;
}

.nav-styled .user-con img {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    object-fit: cover;
    background: #fcf6f9;
    border: 2px solid #FFFFFF;
    padding: 0.2rem;
    box-shadow: 0px 1px 17px rgba(0, 0, 0, 0.06);
}

.nav-styled .user-con h2 {
    color: rgba(34, 34, 96, 1);
}

.nav-styled .user-con p {
    color: rgba(34, 34, 96, 0.6);
}

/* Menu items */
.nav-styled .menu-items {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.nav-styled .menu-items li {
    display: grid;
    grid-template-columns: 40px auto;
    align-items: center;
    margin: 0.6rem 0;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.4s ease-in-out;
    color: rgba(34, 34, 96, 0.6);
    padding-left: 1rem;
    position: relative;
}

.nav-styled .menu-items li i {
    color: rgba(34, 34, 96, 0.6);
    font-size: 1.4rem;
    transition: all 0.4s ease-in-out;
}

.nav-styled .menu-items .active {
    color: rgba(34, 34, 96, 1) !important;
}

.nav-styled .menu-items .active i {
    color: rgba(34, 34, 96, 1) !important;
}

.nav-styled .menu-items .active::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 4px;
    height: 100%;
    background: #222260;
    border-radius: 0 10px 10px 0;
}
